.home {
  position: relative;
  max-height: 56.5rem;
  width: 100%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;

  img {
    height: 100%;
    width: 100%;
    margin: 0;
    object-fit: contain;
  }

  &__main-content {
    position: absolute;
    font-family: inherit;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
    padding: 2rem;
    text-align: center;
    height: 100%;
    width: 100%;
    background: linear-gradient(rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65));
    color: #fff;

    h1 {
      font-size: 6rem;
      letter-spacing: 2px;
    }

    p {
      margin-top: 2rem;
      font-size: 1.6rem;
    }
  }
}

// mobile
@media only screen and (max-width: 767px) {
  .home {
    top: 0; // for navigation

    & img {
      width: 100%;
    }
  }
}

